import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

let store = new Vuex.Store({
    state: {
        employee: [],
        category: [],
        product: [],
        news: []
    },
    mutations: {
        SET_EMPLOYEE: (state, employee) => {
            state.employee = employee;
        },
        SET_CATEGORY: (state, category) => {
            state.category = category;
        },
        SET_PRODUCT: (state, product) => {
            state.product = product;
        },
        SET_NEWS: (state, news) => {
            state.news = news;
        },
        findById: (state, article) => {
            state.employee.article = article;

            // const idx = state.employee.findIndex(t => t.id === id)
            // state.employee[idx] = employee

            // const employees = state.employee.concat()
            // const idx = employees.find(t => t.id === id)
            // const employee = employees[idx]

            // employees[idx] = {...employee }

            // state.employee = employees
        }
    },
    actions: {
        GET_EMPLOYEE({ commit }) {
            return axios('https://87a847af925a.ngrok.io/employee', {
                    method: "GET"
                })
                .then((employee) => {
                    commit('SET_EMPLOYEE', employee.data)
                        // Вызываем мутацию и передаем employee
                    return employee;
                })
                .catch((error) => {
                    console.log(error)
                    return error;
                })
        },
        GET_CATEGORY({ commit }) {
            return axios('https://87a847af925a.ngrok.io/categories', {
                    method: "GET"
                })
                .then((category) => {
                    commit('SET_CATEGORY', category.data)
                        // Вызываем мутацию и передаем employee
                    return category;
                })
                .catch((error) => {
                    console.log(error)
                    return error;
                })
        },
        GET_PRODUCT({ commit }) {
            return axios('https://87a847af925a.ngrok.io/categories/patches', {
                    method: "GET"
                })
                .then((product) => {
                    commit('SET_PRODUCT', product.data)
                    return product;
                })
                .catch((error) => {
                    console.log(error)
                    return error;
                })
        },
        GET_NEWS({ commit }) {
            return axios('https://87a847af925a.ngrok.io/news', {
                    method: "GET"
                })
                .then((product) => {
                    commit('SET_NEWS', product.data)
                    return product;
                })
                .catch((error) => {
                    console.log(error)
                    return error;
                })
        },
        findById({ commit }, article) {
            commit('findById', article)
        }
    },
    getters: {
        EMPLOYEE(state) {
            return state.employee;
        },
        CATEGORY(state) {
            return state.category;
        },
        PRODUCT(state) {
            return state.product;
        },
        NEWS(state) {
            return state.news;
        },
        findByCateg: s => name => s.category.find(t => t.name === name),
        findById: s => article => s.employee.find(t => t.article === article)
    }
})
export default store;