            <p class="employee-item__name"> {{employee_data.name}}</p>
            <p class="employee-item__surname"> {{employee_data.surname}}</p>
            <!-- <p class="employee-item__surname"> {{employee_data.article}}</p> -->
        </figcaption>
    </figure>
</template>
<script>
    export default {
        name: 'employee-item',
        props: {
            employee_data: {
