<template>
    <div class="category__wrap">
        <div class="category__row">
            <CategoryItem v-for="category in CATEGORY" :key="category.article" v-bind:category_data="category" />
        </div>
    </div>
</template>
<script>
    import CategoryItem from '@/components/categories-item'
    import {
        mapActions,
        mapGetters
    } from 'vuex'

    export default {
        name: 'categories',
        data() {
            return {

            }
        },
        components: {
            CategoryItem
        },
        computed: {
            ...mapGetters([
                // this.$store.state.employee
                'CATEGORY'
            ])
        },
        methods: {
            ...mapActions([
                'GET_CATEGORY',
            ])
        },
        mounted() {
            this.GET_CATEGORY()
                .then((response) => {
                    if (response.data) {
                        console.log('Data arrived')
                    }
                })
                .catch((error) => {
                    console.log(error)
                })
        }
    }
</script>